import request from '../utils/request'

// 获取产品详情
export function getProductDetail (id) {
  return request.get(`/host/${id}`)
}
// 开机
export function powerOn (id) {
  return request.post(`/idcsmart_cloud/${id}/on`)
}
// 关机/强制关机
export function powerOff (force, id) {
  if (force) {
    return request.post(`/idcsmart_cloud/${id}/hard_off`)
  } else {
    return request.post(`/idcsmart_cloud/${id}/off`)
  }
}
// 重启/强制重启
export function reboot (force, id) {
  if (force) {
    return request.post(`/idcsmart_cloud/${id}/hard_reboot`)
  } else {
    return request.post(`/idcsmart_cloud/${id}/reboot`)
  }
}
// 获取控制台
export function postVnc (id) {
  return request.post(`/idcsmart_cloud/${id}/vnc`)
}
// 控制台页面
export function getVnc (id) {
  return request.get(`/idcsmart_cloud/${id}/vnc`)
}
// 获取实例状态
export function getStatus (id) {
  return request.get(`/idcsmart_cloud/${id}/status`)
}
// 重置密码
export function resetPassword (params) {
  return request.post(`/idcsmart_cloud/${params.id}/reset_password`, params)
}
// 当前实例是否是救援系统
export function getIsRescue (id) {
  return request.get(`/idcsmart_cloud/${id}/remote_info`)
}
// 救援模式
export function rescue (params) {
  return request.post(`/idcsmart_cloud/${params.id}/rescue`, params)
}
// 退出救援模式
export function exitRescue (id) {
  return request.post(`/idcsmart_cloud/${id}/rescue/exit`)
}
// 重装系统
export function reinstall (params) {
  return request.post(`/idcsmart_cloud/${params.id}/reinstall`, params)
}
// 模板列表
export function getTemplate (id) {
  return request.get(`/idcsmart_cloud/${id}/template`)
}
// 获取可用官方镜像
export function getSystemImage (params) {
  return request.get(`/product/${params.product_id}/idcsmart_cloud/system_image`, { params })
}
// 检查产品是够购买过镜像
export function imageIsBuy (params) {
  return request.get(`/idcsmart_cloud/${params.id}/image/check`, { params })
}
// ssh
export function getSsh (params) {
  return request.get(`/ssh_key`, { params })
}
// 生成购买镜像订单
export function createOrder (params) {
  return request.post(`/idcsmart_cloud/${params.id}/image/order`, params)
}