import React, { useState, useEffect, useRef } from 'react';
import { useTranslation } from "react-i18next";
import { useNavigate } from 'react-router-dom'
import moment from 'moment'
import './cloud.css'
import { Select, Input, Table, Popover, Tooltip } from 'antd';
import ComPagination from '@/components/comPagination/ComPagination.jsx'
import { showDialog } from '@/utils/util'
import * as cloud from '@/api/cloud'
import * as manage from '@/api/cloudManage'
import useMountedRef from '@/utils/UseMountedRef.jsx'
import {
  SearchOutlined,
  EllipsisOutlined
} from '@ant-design/icons';
import PowerModal from '@/components/powerModal/PowerModal';
const { Option } = Select;
const { Search } = Input;
// const search = () => (require("@/assets/images/search.png"))
//const SearchIcon = (props) => <Icon component={search} {...props} />;
export default function CloudList (props) {
  const navigate = useNavigate()
  const mountedRef = useMountedRef();
  const { t } = useTranslation()
  let timer = useRef()
  /* 数据初始化 */
  const id = 30 // 实例默认id 30
  const [center, setCenter] = useState([])
  const [cloudList, setCloudList] = useState([])
  const [total, setTotal] = useState(0)
  const [loading, setLoading] = useState(false)
  // const [tabHeight, settabHeight] = useState(0)
  const [optTitle, setOptTitle] = useState('')
  const [optVisible, setOptVisible] = useState(false)
  const [optType, setOptType] = useState('')
  const [info, setInfo] = useState({})
  const [params, setParams] = useState({
    keywords: '',
    page: 1,
    limit: 20,
    orderby: 'id',
    sort: 'desc',
    data_center_id: ''
  })
  // 搜索框
  const columns = [
    {
      title: 'ID',
      dataIndex: 'id',
      key: 'id',
      render: (_, record) => (
        <>
          <span onClick={() => jump(record)} className='link'>{record.id}</span>
        </>
      )
    },
    {
      title: t("cloud.area"),
      dataIndex: 'area',
      key: 'area',
      render: (_, record) => (
        <div className="area">
          <img src={record.country_code && require(`@/assets/images/cloud/${record.country_code}.png`)} alt="" className="country" />
          <span>{record.city}</span>&nbsp;
          <span>{record.area}</span>
        </div>
      ),
    },
    {
      title: t("menu.product"),
      dataIndex: 'name',
      key: 'name',
      render: (_, record) => (
        <>
          <div>{record.package_name}</div>
          <div className="host-name">{record.name}</div>
        </>
      ),
    },
    {
      title: 'IP',
      dataIndex: 'ip',
      key: 'ip',
      width: 150
    },
    {
      title: t("common.status"),
      dataIndex: 'status',
      key: 'status',
      width: 150,
      render: (_, record) => {
        if (record.status === 'Active') {
          return (
            <>
              <img src={record.power_status && require(`@/assets/images/cloud/${record.power_status}.png`)} alt="" className="power" />
              <span>{powerStatus(record.power_status)}</span>
            </>
          )
        } else {
          return (
            <>
              <div className='status-tag'
                style={{
                  background: record.status === 'Unpaid' ? '#FFE2E5' : record.status === 'Pending' ? '#FFF4DE' :
                    record.status === 'Suspended' ? '#FFF4DE' : '#F2F2F7',
                  color: record.status === 'Unpaid' ? '#F64E60' : record.status === 'Handling' ? '#FFA800' :
                    record.status === 'Reply' ? '#FFA800' : '#9696A3'
                }}
              >{record.status === 'Unpaid' ? t("cloud.Unpaid") : record.status === 'Pending' ? t("cloud.Pending") :
                record.status === 'Active' ? t("cloud.Active") : record.status === 'Suspended' ? t("cloud.Suspended") :
                  record.status === 'Deleted' ? t("cloud.Deleted")
                    : t("cloud.Failed")}</div>
            </>
          )
        }
      }
    },
    {
      title: 'OS',
      dataIndex: 'icon',
      key: 'icon',
      width: 100,
      render: (_, record) => (
        <>
          <Tooltip placement="topLeft" title={record.image_name}>
            {/* <img src={record.icon && require(`@/assets/images/cloud/${record.icon}.png`)} alt="" /> */}
            <img src={'/' + record.icon} alt="" className="cloud-os" />
          </Tooltip>
        </>
      )
    },
    {
      title: t("common.due_time"),
      dataIndex: 'due_time',
      key: 'icon',
      render: (_, record) => (
        <>
          {
            record.due_time === 0 ? '-' : <span>{moment(record.due_time * 1000).format('YYYY-MM-DD HH: mm')}</span>
          }
        </>
      )
    },
    {
      title: t("cloud.manage"),
      dataIndex: 'manage',
      key: 'manage',
      render: (_, record) => (
        <>
          {
            record.status === 'Active' ?
              <div className="com-opt-pop" id={'com-opt' + record.id}>
                <Popover getPopupContainer={() => document.getElementById('com-opt' + record.id)}
              
                  content={
                    <>
                      {
                        record.power_status === 'on' ? (
                          <>
                            <p className="cloud-opt" onClick={() => editItem('off', record)}>{t("cloud.off")}</p>
                            <p className="cloud-opt" onClick={() => editItem('reboot', record)}>{t("cloud.restart")}</p>
                          </>
                        ) : (
                          <>
                            <p className="cloud-opt" onClick={() => editItem('on', record)}>{t("cloud.on")}</p>
                            <p className="cloud-opt" onClick={() => editItem('reboot', record)}>{t("cloud.restart")}</p>
                          </>
                        )
                      }
                    </>
                  } placement="topRight">
                  <p className="opt-icon"><EllipsisOutlined /></p>
                </Popover>
              </div> : null
          }
        </>
      )
    }
  ];
  /* 数据请求 */
  // 数据中心
  const getDataList = async () => {
    try {
      const res = await cloud.getDataCenter(id)
      const data = res.data.data.list
      if (mountedRef.current) {
        setCenter(data)
      }
    } catch (error) {
      showDialog(error.data.msg, 'error')
    }
  }
  // 实例列表
  const getCloudList = async () => {
    try {
      setLoading(true)
      const res = await cloud.getCloudList(params)
      if (mountedRef.current) {
        setCloudList(res.data.data.list)
        setTotal(res.data.data.count)
        setLoading(false)
      }
    } catch (error) {
      showDialog(error.data.msg, 'error')
      setLoading(false)
    }
  }

  useEffect(() => {
    getCloudList()
    getDataList()
    return () => clearInterval(timer.current)
  }, [])

  const changePage = (page, size) => {
    setParams(params => {
      params.limit = size
      params.page = page
      return params
    })
    getCloudList()
  }

  /* 事件操作 */
  // 跳转
  const jump = (record) => {
    navigate(`/cloud_detail?id=${record.id}`)
  }
  // 选择数据中心
  const changeDataCenter = (e) => {
    setParams(params => {
      params.data_center_id = e
      params.page = 1
      return params
    })
    getCloudList()
  }
  const changeKey = (e) => {
    setParams(params => {
      const temp = params
      params.keywords = e
      params.page = 1
      return temp
    })
    getCloudList()
  }
  // 显示状态
  const powerStatus = (e) => {
    switch (e) {
      case 'on':
        return t('cloud.on')
      case 'off':
        return t('cloud.off')
      case 'operating':
        return t('cloud.operating')
      case 'fault':
        return t('cloud.fault')
      default:
        return ''
    }
  }
  const optCancel = () => {
    setOptVisible(false)
  }
  const editItem = (type, row) => {
    setInfo(row)
    setOptVisible(true)
    setOptType(type)
    switch (type) {
      case 'on':
        return setOptTitle(t('cloud.tip16'))
      case 'off':
        return setOptTitle(t('cloud.tip17'))
      case 'reboot':
        return setOptTitle(t('cloud.tip18'))
      default:
        return setOptTitle('title')
    }
  }
  // 轮询实例状态
  const pollingStatus = () => {
    timer.current = setInterval(async () => {
      try {
        const res = await manage.getStatus(info.id)
        if (res.data.data.status !== "operating") {
          clearInterval(timer.current)
          getCloudList()
          return null
        }
      } catch (error) {
      }
    }, 2000)
  };
  // 操作成功弹窗
  const showDia = (msg, status) => {
    showDialog(msg, status, () => {
      optCancel()
    })
  }
  // 开机
  const powerOn = async () => {
    try {
      const res = await manage.powerOn(info.id)
      if (res.data.status === 200) {
        pollingStatus()
        showDia(res.data.msg, 'success')
      }
    } catch (error) {
      showDia(error.data.msg, 'error')
    }
  }
  // 关机
  const powerOff = async (force) => {
    try {
      const res = await manage.powerOff(force, info.id)
      if (res.data.status === 200) {
        pollingStatus()
        showDia(res.data.msg, 'success')
      }
    } catch (error) {
      showDia(error.data.msg, 'error')
    }
  }
  // 重启
  const reboot = async (force) => {
    try {
      const res = await manage.reboot(force, info.id)
      if (res.data.status === 200) {
        pollingStatus()
        showDia(res.data.msg, 'success')
      }
    } catch (error) {
      showDia(error.data.msg, 'error')
    }
  }
  // 确认操作按钮
  const onFinish = (force) => {
    switch (optType) {
      case 'on':
        return powerOn(force)
      case 'off':
        return powerOff(force)
      case 'reboot':
        return reboot(force)
      default:
        break;
    }
  }
  return (
    <div className="com-content cloud-list">
      <div className="com-card">
        <div className="header">
          <h1 className="t-tit">{t("cloud.product_list")}</h1>
          <div className="r-search">
            <Select
              getPopupContainer={triggerNode => triggerNode.parentElement}
              placeholder={t("cloud.data_center") + t("cloud.screen")}
              onChange={changeDataCenter}
              dropdownMatchSelectWidth={false}
              optionLabelProp="label" allowClear>
              {
                center.map(item => {
                  return (
                    item.area.map(area => {
                      return (
                        <Option value={area.id} key={area.id} label={item.country + '-' + item.city + '-' + area.area}>{item.country}-{item.city}-{area.area}</Option>
                      )
                    })
                  )
                })
              }
            </Select>
            <Search placeholder={t("input")} onSearch={changeKey} allowClear prefix={<SearchOutlined />} className="keyword"></Search>
          </div>
        </div>
        <Table columns={columns} dataSource={cloudList} loading={loading}
          rowKey={record => record.id} pagination={false} className="cloud-tab" />
        <ComPagination total={total} page={params} changePage={changePage} ></ComPagination>
      </div>
      {
        optVisible && <PowerModal info={info} optTitle={optTitle} optType={optType}
          optCancel={optCancel} onFinish={onFinish}
        ></PowerModal>
      }
    </div>
  )
}
