import React from 'react'
import { useTranslation } from "react-i18next";
import { message, Select, Input, Table, Popover, Tooltip, Form, Modal, Checkbox, Button } from 'antd';
import './PowerModal.css'
export default function PowerModal (props) {
  const { t } = useTranslation()
  const [form] = Form.useForm()
  const optCancel = () => {
    props.optCancel()
  }
  const onFinish = () => {
    const force = form.getFieldsValue().remember
    props.onFinish(force)
  }
  return (
    <>
      {/* 开关机，重启 */}
      <Modal title={props.optTitle} forceRender
        visible={true} onCancel={optCancel}
        footer={null} className="power-modal" closable={false}>
        {
          (
            <Form form={form}
              initialValues={{
                host_name: props.info.name,
                remember: false,
              }}
              onFinish={onFinish}
              autoComplete="off"
            >
              <Form.Item label={t("cloud.host_name")} colon={false} name="host_name">
                <Input disabled />
              </Form.Item>
              {
                props.optType !== 'on' && (
                  <>
                    <p className='tip'>{t("cloud.tip19")}</p>
                    <Form.Item colon={false}
                      name="remember"
                      valuePropName="checked"
                      // rules={[
                      //   {
                      //     validator: (_, value) =>
                      //       value ? Promise.resolve() : Promise.reject(new Error(t("cloud.tip21"))),
                      //   },
                      // ]} 
                      className="force-off">
                      <Checkbox>{t("cloud.tip20")}</Checkbox>
                    </Form.Item>
                  </>
                )
              }
              <Form.Item className="opt-btn">
                <Button type="primary" htmlType="submit" className="sure">
                  {t("common.sure")}
                </Button>
                <Button onClick={optCancel} className="cancel">
                  {t("common.cancel")}
                </Button>
              </Form.Item>
            </Form>
          )
        }
      </Modal>
    </>
  )
}
